<template>
    <v-container id="template">
        <v-btn  id="print" class="blue white--text mr-4 mb-5" @click="printReport()">Print</v-btn>
        <v-btn id="print" class="red white--text mb-5" to="/cetaklabelkarton" link>Back</v-btn>
        <p id="print">Catatan : <br> *Kertas Print = Envelope DL 110x220 mm <br> *Orientasi = Landscape</p>
        <template id="template">
                <table style="border-collapse:separate; border-spacing:5em 1px;">
                    <template v-for="i in karton">   
                    <tr v-for="j in i['Jumlah']" :key='j.id'>
                        <td>
                            <table style="width:302px;vertical-align:center" cellspacing=0>
                                <tr>
                                    <td style="text-align: center;font-size:70pt;font-family: 'Times New Roman', Times, serif;"><b>{{ i["Kode Barang"] }}</b></td>
                                </tr>
                                <tr>
                                    <td style="text-align: center;">
                                        <barcode :value="i['Barcode']" format="CODE128" height="60px"  width="2">Show this if the rendering fails.</barcode>
                                    </td>
                                </tr>
                                <tr>
                                    <td style="text-align: center;font-size:38pt;font-family: 'Times New Roman', Times, serif;"><b>{{ i["No Bets"] }}</b></td>
                                </tr>
                                <tr>
                                    <td style="text-align: center;font-size:38pt;font-family: 'Times New Roman', Times, serif;"><b>{{ i["Tanggal Produksi"] }}</b></td>
                                </tr>
                                <tr>
                                    <td style="text-align: center;font-size:38pt;font-family: 'Times New Roman', Times, serif;"><b>{{ i["Isi1Karton"] }} Lusin</b></td>
                                </tr>
                            </table>
                        </td>
                        <td width="200px"></td>
                        <td>
                            <table style="width:302px;" cellspacing=0>
                                <tr>
                                    <td style="text-align: center;font-size:70pt;font-family: 'Times New Roman', Times, serif;"><b>{{ i["Kode Barang"] }}</b></td>
                                </tr>
                                <tr>
                                    <td style="text-align: center;">
                                        <barcode :value="i['Barcode']" format="CODE128" height="60px" width="2" >Show this if the rendering fails.</barcode>
                                    </td>
                                </tr>
                                <tr>
                                    <td style="text-align: center;font-size:38pt;font-family: 'Times New Roman', Times, serif;"><b>{{ i["No Bets"] }}</b></td>
                                </tr>
                                <tr>
                                    <td style="text-align: center;font-size:38pt;font-family: 'Times New Roman', Times, serif;"><b>{{ i["Tanggal Produksi"] }}</b></td>
                                </tr>
                                <tr>
                                    <td style="text-align: center;font-size:38pt;font-family: 'Times New Roman', Times, serif;"><b>{{ i["Isi1Karton"] }} Lusin</b></td>
                                </tr>
                            </table>
                        </td>
                    </tr>
                    </template>
                </table>
        </template>
    </v-container>
</template>
<script>
import VueBarcode from 'vue-barcode';

    export default {
        components: {
            'barcode': VueBarcode
        },
        data() {
            return {
                karton: [],
            }
        },
        methods: {
            getData() {
                var uri = this.$apiUrl + 'cetaklabelkarton/cetak'
                this.$http.get(uri).then(response => {
                    this.karton = response.data
                    for(var i in this.karton){
                        if((this.karton[i]["Isi1Karton"]*12) < 100){
                            this.karton[i]["Barcode"] = this.karton[i]["Kode Barang"] + this.karton[i]["No Bets"] + '0' + (this.karton[i]["Isi1Karton"])*(12)
                        }
                        else{
                            this.karton[i]["Barcode"] = this.karton[i]["Kode Barang"] + this.karton[i]["No Bets"] + (this.karton[i]["Isi1Karton"])*(12)
                        }
                    }
                })
            },

            printReport() {
                window.print()
            },

            Activity(key) {
                // untuk memposting aktivitas user yang login
                var activity = new FormData
                activity.append('activity', key)
                var uri = this.$apiUrl + 'activity'
                this.$http.post(uri, activity).then(response => {}).catch(error => {
                    this.errors = error
                })
            },
        },
        mounted() {
            this.getData();
        },
    }
</script>
<style scoped>
    @media print {
        #print {
            display: none;
        }

        #template {
            background-color: white
        }
      
    }
    #tabel{
        border-collapse: collapse;
    }
    #tabel #th, #td {
        border: 1px solid black;
    }
</style>